	export default {
		data() {
			return {
				type:'',//健康商城 community  自营  customer
				buylist: [],
				payType: 1, //1微信 2支付宝
				totalNum:0,
				total:0,
				goodsPrice:0,
				fee:0,
				// addressStatus:false,//是否有默认地址
				addressData: {
					name: '',
					mobile: '',
					regions: '',
                    provinceName:'',
                    cityName:'',
                    areaName:'',
					detailAddress: ''
				},
                instanceId:'',
                
                activat:false
			}
		},
		computed: {
			communityId() {
				return this.$store.getters.communityId 
			}
		},
		onLoad(options) {
			//健康商城 community  自营  customer   私家医生  doctor    
			this.type =options.type
		
            this.instanceId = options.instanceId ? options.instanceId : ''
            
			if(options.webpid){
				let cartList = [];
				let obj = {
					id: options.productId,
					image: options.url,
					title: options.name,
					price: options.price,
					number: 1
				};
				cartList.push(obj)
				uni.setStorage({
					key: 'buylist',
					data: cartList
				})
			}
			
			
            this.getPostFee(this.type)
			
			uni.getStorage({
				key: 'deliAddress',
				success: (e) => {
					this.addressData = e.data;
                    if(this.addressData.name !=undefined && this.addressData.name!=''){
                        console.log(this.addressData)
                        this.activat = true
                    }
				}
			})
		},
		onShow() {
			let pages = getCurrentPages();
			let currPage = pages[pages.length-1];
        
			if(currPage.data.addressData.name==undefined || currPage.data.selectedAddress==''){
			
			}else{
				this.addressData = currPage.data.addressData
                if(this.addressData.name !=undefined && this.addressData.name!=''){
                    this.activat = true
                }
			}
		},
		methods: {
            calcTotal:function(){
                //商品数据
                uni.getStorage({
                	key: 'buylist',
                	success: (res) => {
                		this.buylist = res.data;
                		this.goodsPrice = 0;
                		let list = this.buylist;
                		//合计 计算总价
                		list.forEach(item => {
                			console.log(item);
                			this.goodsPrice += item.price * item.number;
                			this.totalNum += item.number;
                		})
                        this.fee = this.minimum - this.goodsPrice>0 ? this.expenses:0
                		this.goodsPrice+= this.fee
                		// this.goodsPrice = this.goodsPrice - this.deduction + this.freight;
                	}
                });
            },
            getPostFee(type){
            	// 健康商城 community  服务站商城  customer
            	switch (type){
                    case 'doctor':
                    	
                    	this.expenses = 0
                    	this.minimum = 0
                    	
                    	this.calcTotal()
                    		
                    	break;
            		case 'community':
            			this.$api.communityGetPostage().then(res=>{
            				if(res.status=="OK"){
								if(res&&res.data&&res.data.expenses){
									this.expenses = res.data.expenses/100
									this.minimum = res.data.minimum/100
								}
            					this.calcTotal()
            				}
            			}).catch(err=>{
            				console.log(err);
            			})
            			break;
            		case 'customer':
            			this.$api.customerGetPostage().then(res=>{
            				if(res.status=="OK"){
								if(res&&res.data&&res.data.expenses){
									this.expenses = res.data.expenses/100
									this.minimum = res.data.minimum/100
								}
                                this.calcTotal()
            				}
            			}).catch(err=>{
            				console.log(err);
            			})
            			break;
            	}
            	
            },
            clickJisuan:function(tag,index){
                if(tag == 1){   //减
                    if(this.buylist[index].number>1){
                        this.buylist[index].number = this.buylist[index].number - 1
                        this.totalNum = 0
                        var price = 0
                        this.buylist.forEach(item => {
                            price += item.price * item.number;
                            this.totalNum += item.number;
                        })
                        this.fee = this.minimum - price>0 ? this.expenses:0
                        this.goodsPrice = price + this.fee
                    }else{
                        if(this.buylist.length>1){
                            this.buylist.splice(index,1)
                            this.totalNum = 0
                            var price = 0
                            this.buylist.forEach(item => {
                                price += item.price * item.number;
                                this.totalNum += item.number;
                            })
                            this.fee = this.minimum - price>0 ? this.expenses:0
                            this.goodsPrice = price + this.fee
                        }
                    }
                }else{
                    this.buylist[index].number = this.buylist[index].number + 1
                    var price = 0
                    this.totalNum = 0
                    this.buylist.forEach(item => {
                    	this.totalNum += item.number;
                        price += item.price * item.number;
                    })
                    this.fee = this.minimum - price>0 ? this.expenses:0
                    this.goodsPrice = price + this.fee
                }
            },
			//显示优惠券面板
			numberChange(data) {
				this.number = data.number;
			},
			changePayType(type) {
				this.payType = type;
			},
			//监听image加载完成
			onImageLoad(key, index) {
				this.$set(this[key][index], 'loaded', 'loaded');
			},
			//监听image加载失败
			onImageError(key, index) {
				this[key][index].image = '/static/healthy-mall/errorImage.jpg';
			},
			submit() {
				// console.log(this.addressData);
				let addressData = this.addressData
	
				if(addressData.name==''){
					// uni.showToast({
					// 	title: '地址有误',
					// 	icon:'none'
					// });
					return
				}
                var address = {"provinceName":this.addressData.provinceName,"cityName":this.addressData.cityName,"areaName":this.addressData.areaName,"address":this.addressData.detailAddress}
                address = JSON.stringify(address)
                
                if(this.type == 'community' || this.type == 'doctor'){
                    let items = []
                    this.buylist.map(item=>{
                    	items.push({
                    		num: item.number,
                    		productId: item.id,
                    		type: item.productType?item.productType:'COMMUNITY_PRODUCT'
                    	})
                    })
                    var order_type = 'MALL'
                    var data
                    if(this.type == 'doctor'){
                        order_type = 'DOCTOR'
                        data = {
                        	name:addressData.name,
                        	communityId:this.communityId,
                        	address:address,
                        	contcatPhone:addressData.mobile,
                        	items:items,
                            source:'MINGWO',
                            instanceId:this.instanceId,
                            orderType:order_type
                        }
                    }else if(this.instanceId != ''){
                        data = {
                        	name:addressData.name,
                        	communityId:this.communityId,
                        	address:address,
                        	contcatPhone:addressData.mobile,
                        	items:items,
                            source:'MINGWO',
                            instanceId:this.instanceId
                        }
                    }else{
                        data = {
                        	name:addressData.name,
                        	communityId:this.communityId,
                        	address:address,
                        	contcatPhone:addressData.mobile,
                        	items:items,
                            source:'MINGWO',
                            instanceId:this.instanceId,
                            orderType:order_type
                        }
                    }
                    
                    this.$api.constitutionOrderAddOrder(data).then(res=>{
                    	console.log(res);
                        if(res.status == 'OK'){
                           
                            uni.requestPayment({
                                provider: 'wxpay',
                                timeStamp:res.data.payInfo.timeStamp,
                                nonceStr: res.data.payInfo.nonceStr,
                                package: res.data.payInfo.packageValue,
                                signType: 'MD5',
                                paySign: res.data.payInfo.sign,
                                success: function (payres) {
                                    
                                    uni.navigateTo({
                                        url:'../../order-detail/order-detail?type=99&orderid='+ res.data.id
                                    })
                                },
                                fail: function (err) {
                                    console.log('fail:' + JSON.stringify(err));
                                    
                                }
                            });
                        }else{
                            uni.showToast({
                                title: res.message,
                                icon:'none'
                            })
                        }
                        
                    }).catch(err=>{
                    	console.log(err);
                    })
                }else if(this.type == 'customer'){
                    this.$api.constitutionOrderAddOrder({
                    	name:addressData.name,
                    	address:address,
                    	contcatPhone:addressData.mobile,
                    	items:[],
                        source:'MINGWO'
                    }).then(res=>{
                    	console.log(res);
                        if(res.status == 'OK'){
                            
                            uni.requestPayment({
                                provider: 'wxpay',
                                timeStamp:res.data.payInfo.timeStamp,
                                nonceStr: res.data.payInfo.nonceStr,
                                package: res.data.payInfo.packageValue,
                                signType: 'MD5',
                                paySign: res.data.payInfo.sign,
                                success: function (payres) {
                                    
                                    uni.navigateTo({
                                        url:'../../order-detail/order-detail?type=99&orderid='+ res.data.id
                                    })
                                },
                                fail: function (err) {
                                    console.log('fail:' + JSON.stringify(err));
                                    
                                }
                            });
                        }else{
                            uni.showToast({
                                title: res.message,
                                icon:'none'
                            })
                        }
                    }).catch(err=>{
                    	console.log(err);
                    })
                }
				
				// {
				//   "address": "string",
				//   "contcatPhone": "string",
				//   "items": [
				//     {
				//       "num": 0,
				//       "productId": "string",
				//       "type": "DIET_PLAN"
				//     }
				//   ],
				//   "name": "string"
				// }
				
			},
			stopPrevent() {}
		},
		filters: {
			toFixed: function(value) {
				value = parseFloat(value)
				return value.toFixed(2);
			}
		}
	}
